export function addAThemeToBook(themeId = '52') {
    store.dispatch({
        type: 'book_stat/collectOneTheme',
        themeId,
        collectAt: Date.now(),
    })
}

// 
export function addACardToStove(timeSamp, cardId = '9115') {
    store.dispatch({
        type: 'stove_list/putACardInStove',
        cardId,
        startToRefineAt: timeSamp || Date.now(),
    })
}

