import getData from './getData'

export function getTimeFormat(endTime) {
    let secondTime = parseInt(endTime)//Number
    let min = 0// 
    let h = 0// 
    let result = ''
    if (secondTime >= 60) {//60
        min = parseInt(secondTime / 60)//60
        secondTime = parseInt(secondTime % 60)//
        if (min >= 60) {//60
            h = parseInt(min / 60)//60
            min = parseInt(min % 60) //60
        }
    }
    result = `${h.toString().padStart(2, '0')}:${min.toString().padStart(2, '0')}:${secondTime.toString().padStart(2, '0')}`
    return result
}

// 
export function getPercent(objVal, totalVal, toFixedNum = 1) {
    let res = 100 * parseFloat(objVal) / parseFloat(totalVal)
    res = res.toFixed(toFixedNum)
    return res
}

// 
export function getlvl(exp) {
    let res = Math.floor(Math.pow(exp / 10, 1 / 3))
    return res
}

// 
export function getRenderExp(exp) {
    let resNum, res
    let expNum = parseInt(exp, 10)
    if (expNum > 10000) {
        resNum = (expNum / 10000).toFixed(1)
        res = resNum + ''
    } else {
        res = exp.toString()
    }
    return res
}

