import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';

import * as dpa from '../dispatchActionWithBusiness'

import './PanelSettings.css'

const { confirm } = Modal;

function PanelSettings(props) {
    const [showDebug, setShowDebug] = useState(false)
    const [friendsNum, setFriendsNum] = useState(5)

    function resetLocal() {
        localStorage.clear();
        window.location.reload()
    }

    function handleClick() {
        setFriendsNum(friendsNum + 1)
    }

    useEffect(() => {
        confirm({
            title: '',
            icon: <ExclamationCircleOutlined />,
            onOk() {
                setShowDebug(true)
            },
            onCancel() {
            },
        })
    }, [])
    return (
        <>
            {showDebug && (
                <div className="settings_w">
                    <p></p>
                    <button onClick={handleClick}>: {friendsNum}</button>
                    <p></p>
                    <button onClick={e => props.addExp(100)}></button>
                    <button onClick={e => props.setExp(3251)}>lv. 5</button>
                    <button onClick={e => props.setExp(10001)}>lv. 10</button>
                    <button onClick={e => props.setUserName('Larria' + Date.now().toString().slice(9))}></button>
                    <button onClick={e => dpa.updateBagListWithATheme()}></button>
                    <button onClick={e => dpa.addAThemeToBook()}></button>
                    <button onClick={e => dpa.addACardToStove(Date.now(), '38')}></button>
                    <button onClick={e => dpa.addACardToStove(Date.now() - (1000 * 3600 * 24 * 365), '38')}></button>
                    <button onClick={e => dpa.addACardToStove(Date.now() - (1000 * 3500), '41')}></button>
                    <button onClick={e => dpa.updateStoveList([])}></button>
                    <button onClick={resetLocal}></button>
                </div>)
            }
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        userName: state.userName,
        avatar: state.avatar,
        exp: state.exp,
        gold: state.gold,
        power: state.power,
        bookStat: state.bookStat
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        setUserName: (name) => {
            let action = {
                type: 'setUserName',
                name
            }
            dispatch(action);
        },
        addExp: (exp) => {
            let action = {
                type: 'addExp',
                exp
            }
            dispatch(action);
        },
        setExp: (exp) => {
            let action = {
                type: 'setExp',
                exp
            }
            dispatch(action);
        }
    }
}

PanelSettings.defaultProps = {
}

export default connect(mapStateToProps, mapDispatchToProps)(PanelSettings);